import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import _axios from "axios";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";

const useStyles = makeStyles({
  root: {
    width: "100%",
    maxWidth: 500,
    display: "flex",
    alignItems: "flex-start",
    flexDirection: "column",
  },
});

function RepoView() {
  const { userName, repoName } = useParams();
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [sCount, setSCount] = useState(0);
  const [url, setURL] = useState("");
  const classes = useStyles();

  useEffect(
    () =>
      _axios(`https://api.github.com/repos/${userName}/${repoName}`)
        .then((value) => {
          setName(value.data.full_name);
          setDescription(value.data.description);
          setSCount(value.data.stargazers_count);
          setURL(value.data.html_url);
        })
        .catch(console.log),
    []
  );
  return (
    <>
      <Box sx={{ display: "flex", justifyContent: "center" }} p={10}>
        <div className={classes.root}>
          <Typography variant="h3" gutterBottom>
            <a
              href={url}
              style={{ textDecoration: "none" }}
              target="_blank"
              rel="noopener noreferrer"
            >
              {name}
            </a>
          </Typography>
          <Typography variant="h5" gutterBottom>
            Description
          </Typography>
          <Typography variant="body" component="p" gutterBottom>
            {!description ? "No description" : description}
          </Typography>
          <Typography variant="h5" gutterBottom>
            Stargazers Count
          </Typography>
          <Typography variant="body2" style={{ marginLeft: 10 }}>
            {sCount}
          </Typography>
        </div>
      </Box>
    </>
  );
}

export default RepoView;
